import React, { useEffect, useState, useContext } from "react";
import { Container, Table, Button, Form, ButtonGroup } from "react-bootstrap";
import { observer } from "mobx-react-lite";
import { Context } from "../index";
import { toast } from "react-custom-alert";

const AdminReviews = observer(() => {
  const { review } = useContext(Context);
  const [searchTerm, setSearchTerm] = useState("");
  const [filterRating, setFilterRating] = useState("");
  const [filterStatus, setFilterStatus] = useState("");

  useEffect(() => {
    review.loadReviews({
      search: searchTerm,
      rating: filterRating,
      status: filterStatus,
    });
  }, [review, searchTerm, filterRating, filterStatus]);

  const handleDelete = async (reviewId) => {
    if (window.confirm(" ,     ?")) {
      try {
        await review.deleteReview(reviewId);
        toast.success("  ");
      } catch (error) {
        toast.error("   ");
      }
    }
  };

  const handleStatusChange = async (reviewId, newStatus) => {
    try {
      await review.updateReviewStatus(reviewId, newStatus);
      toast.success("   ");
    } catch (error) {
      toast.error("    : " + error.message);
    }
  };

  const filteredReviews =
    Array.isArray(review.reviews) && review.reviews.length > 0
      ? review.reviews.filter((review) => {
          if (!review) return false;
          const matchesSearch =
            review.pattern?.name
              ?.toLowerCase()
              .includes(searchTerm.toLowerCase()) ||
            review.comment?.toLowerCase().includes(searchTerm.toLowerCase());
          const matchesRating =
            filterRating === "" || review.rating === parseInt(filterRating);
          const matchesStatus =
            filterStatus === "" || review.status === filterStatus;
          return matchesSearch && matchesRating && matchesStatus;
        })
      : [];

  return (
    <Container style={{ minHeight: "90vh", color: "#f7f7f7" }}>
      <h2 className="mt-4 mb-4"> </h2>

      <div className="mb-4" style={{ display: "flex", gap: "20px" }}>
        <Form.Group style={{ flex: 1 }}>
          <Form.Control
            type="text"
            placeholder="     "
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            style={{
              background: "#27282a",
              color: "#f7f7f7",
              border: "1px solid #3d3d3d",
            }}
          />
        </Form.Group>

        <Form.Group style={{ width: "200px" }}>
          <Form.Select
            value={filterRating}
            onChange={(e) => setFilterRating(e.target.value)}
            style={{
              background: "#27282a",
              color: "#f7f7f7",
              border: "1px solid #3d3d3d",
            }}
          >
            <option value=""> </option>
            <option value="5">5 </option>
            <option value="4">4 </option>
            <option value="3">3 </option>
            <option value="2">2 </option>
            <option value="1">1 </option>
          </Form.Select>
        </Form.Group>

        <Form.Group style={{ width: "200px" }}>
          <Form.Select
            value={filterStatus}
            onChange={(e) => setFilterStatus(e.target.value)}
            style={{
              background: "#27282a",
              color: "#f7f7f7",
              border: "1px solid #3d3d3d",
            }}
          >
            <option value=""> </option>
            <option value="PENDING"> </option>
            <option value="APPROVED"></option>
            <option value="REJECTED"></option>
          </Form.Select>
        </Form.Group>
      </div>

      <Table striped bordered hover variant="dark">

        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {filteredReviews.map((review) => (
            <tr key={review.id}>
              <td>{new Date(review.date).toLocaleDateString()}</td>
              <td>{review.pattern?.name || "  "}</td>
              <td>{review.user?.email || "  "}</td>
              <td>{review.rating}/5</td>
              <td>{review.comment}</td>
              <td>
                <ButtonGroup size="sm">
                  <Button
                    variant={
                      review.status === "APPROVED"
                        ? "success"
                        : "outline-success"
                    }
                    onClick={() => handleStatusChange(review.id, "APPROVED")}
                  >
                    
                  </Button>
                  <Button
                    style={{ marginLeft: "5px" }}
                    variant={
                      review.status === "REJECTED" ? "danger" : "outline-danger"
                    }
                    onClick={() => handleStatusChange(review.id, "REJECTED")}
                  >
                    
                  </Button>
                </ButtonGroup>
              </td>
              <td>
                <Button
                  variant="danger"
                  size="sm"
                  onClick={() => handleDelete(review.id)}
                >
                  
                </Button>
              </td>
            </tr>
          ))}
        </tbody>
      </Table>

      {filteredReviews.length === 0 && (
        <div className="text-center mt-4">
          <p>  </p>
        </div>
      )}
    </Container>
  );
});

export default AdminReviews;
